const TEST_ENVIRONMENT = false

const CONTACTS = [{
    contact: {
        username: 'xyz',
        firstName: 'x',
        lastName: 'z',
        email: 'x',
        id: 2
    },
    notif: {
        sender: 'xyz',
        receiver: 'abc',
        message: 'Hi',
        count: 1,
        date: '4: 17',
        id: 1
    }
}, {
    contact: {
        username: 'yst',
        firstName: 'y',
        lastName: 't',
        email: 'x',
        id: 3
    },
    notif: {
        sender: 'yst',
        receiver: 'abc',
        message: 'Hi',
        count: 0,
        date: '9:04',
        id: 2
    }
}]

export default {
    TEST_ENVIRONMENT,
    CONTACTS,
    date: (date) => {
        const current = new Date()
        const newTime = date || current.toLocaleTimeString()
        const M = newTime.substring(8)
        const bf = newTime[5] === ':' ? newTime.substring(0, 5) : newTime.substring(0, 4)
        return bf + ' ' + M
    },
    capitalize: (contact) => () => {
        let firstname = contact.firstName//.toUpperCase()
        let lastname = contact.lastName//.toUpperCase()

        return `${firstname} ${lastname}`
    }
}