import { Box, Stack, Typography } from "@mui/material"
import UserAvatar from "./UserAvatar"
import helper from "../helper/helper"

const Message = ({message, contact}) => {
    // TODO: Open the files
    const getFiles = () => {
        const files = message.files
        if (files.length === 0) {
            return 
        }

        // Open file
        return files.map(file => (
            <Box>
                {file}
            </Box>
        ))
    }

    // Messages sent by user
    if (message.receiver === contact?.id) {  
        return (
            <Box> 
                <Stack  
                sx={{ 
                    m: 2,
                    mb: 4,
                    backgroundColor: 'purple', 
                    color: 'aliceblue',
                    float: 'right',
                    borderRadius: 4,
                    borderBottomRightRadius: 0,
                    minWidth: {xs: '20vw', md: '9vw'},
                    maxWidth: {xs: '45vw', sm:'35vw', md: '20vw'},
                    boxShadow: 4
                }}>
                    <Box
                    sx={{
                        p: 2,
                        pb: 1
                    }}
                    >
                        <Typography sx={{float: 'right',}}>
                            {message?.string || 'okay'}    
                        </Typography> 
                    </Box>
                    <Box sx={{
                        position: 'relative',
                        left: 10,
                        bottom: 1
                    }}>
                        <Typography sx={{float: 'left'}} fontSize={11}>
                            {helper.date(message.time)}
                        </Typography>
                    </Box>
                    {/**Display files here */}
                </Stack>
            </Box>
        )
    }

   // Messages received by user
   return (
    <Stack direction={'row'}>
        <Box 
          sx={{
            marginTop: 0.5,
          }}>
            <UserAvatar person={contact} />
        </Box>
        <Stack
        sx={{
            m: 2,
            mb: 4,
            backgroundColor: 'aliceblue', 
            color: '#555555',
            float: 'left',
            borderRadius: 2,
            borderTopLeftRadius: 0,
            boxShadow: 8,
            minWidth: {xs: '20vw', md: '9vw'},
            maxWidth: {xs: '45vw',sm:'35vw', md: '20vw'},
        }}
        >
            <Box
                sx={{
                    p: 2,
                    pb: 1
                }}
            >
                <Typography sx={{float: 'left'}}>
                    {message?.string || 'okay'}    
                </Typography> 
            </Box>
            {/**Display files here */}
            {/**Add date to the bottom of the message */}
            <Box
            sx={{
                position: 'relative',
                right: 5
            }}
            >
                <Typography sx={{float: 'right'}} fontSize={11}> 
                    {helper.date(message.time)}    
                </Typography> 
            </Box>
        </Stack>
    </Stack>
   )
}

export default Message